@use "sass:math";

@use 'breakpoints' as *;

$grid-columns: 7;
$grid-gaps: (
    "0": 0,
    "1": 10px,
    "2": 24px,
    "3": 30px,
);

$layout-values: flex-start, flex-end, center, space-between, space-around;



// justify content classes
@each $val in $layout-values {
    .justify-#{$val} {
        justify-content: $val;
    }
}

// base layout classes
.container {
    width: 100%;
    // max-width: 1200px;
    margin: 0 auto;
    //padding: 0 20px;
    box-sizing: border-box;
}

.row {
    display: flex;
    flex-flow: row;
    gap: 14px;

    .logo {
        flex-basis: 30%;
      }
      
      .title {
        flex-basis: 70%;
      }
      .collumn {
        flex-basis: 50%;
      }
      .col-3 {
        flex: 33.33%;
      }

      @media screen and (max-width: 767px) {
        flex-direction: column;
        
        .logo {
          flex-basis: 100%;
        }
        
        .title {
          flex-basis: 100%;
        }
       
      }
    
}

// grid gaps
@each $key, $val in $grid-gaps {
    .gap-#{$key} > * {
        padding: $val;
    }
    .gap-#{$key} {
        margin-left: -$val;
        margin-right: -$val;
    }
}

// col classes
@include xs {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-xs {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}

@include sm {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-sm {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}

@include md {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-md {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}

@include lg {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-lg {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}

@include xl {
    @for $i from 1 through $grid-columns {
        .col-#{$i}-xl {
            box-sizing: border-box;
            flex-grow: 0;
            width: math.div($i * 100%, $grid-columns);
        }
    }
}
